<template>
  <CCard>
    <CCardBody>
      <CDataTable :items="items" :fields="fields" table-filter items-per-page-select :items-per-page="5" hover sorter pagination>
        <template #show_details="{item, index}">
          <td class="py-2">
            <CButton color="primary" variant="outline" square size="sm" @click="toggleDetails(item, index)">
              Show
            </CButton>
          </td>
        </template>
      </CDataTable>
    </CCardBody>
  </CCard>
</template>
<script>
export default {
  name: 'AdvancedTables',
  data() {
    return {
      items: [{ time: '2021-03-15 18:25', subject: 'Test-技術問題', number: '8516359843', type: '技術問題', level: '緊急', status: '等待回覆' }],
      details: [],
      collapseDuration: 0,
    };
  },
  computed: {
    fields() {
      return [
        {
          key: 'time',
          label: '建立時間',
          _style: 'min-width:80px',
        },
        {
          key: 'subject',
          label: '主旨',
          _style: 'min-width:200px',
        },
        {
          key: 'number',
          label: '單號',
        },
        {
          key: 'type',
          label: '類型',
        },
        {
          key: 'level',
          label: '優先等級',
        },
        {
          key: 'status',
          label: '狀態',
        },
        {
          key: 'show_details',
          label: '',
          _style: 'width:1%',
          sorter: false,
          filter: false,
        },
      ];
    },
  },
  methods: {
    toggleDetails(item) {
      this.$router.push('/support/TicketContent' + '/' + item.number);
    },
  },
};
</script>
